<template>
  <div class="reg-wrapper">
    <yd-navbar title="">
        <router-link to="/login" slot="left">
            <yd-navbar-back-icon>Login</yd-navbar-back-icon>
        </router-link>


       
    </yd-navbar>
    <h1 class="bigtitle">Sign up</h1>
    <yd-cell-group>

        <yd-cell-item>
          <yd-input slot="right" type="tel" v-model="phone" placeholder="Phone number" regex="mobile" required="true"></yd-input>
          <yd-button slot="right" type="warning">获取短信验证码</yd-button>
        </yd-cell-item>

        <yd-cell-item>
              <yd-input slot="right" type="password" v-model="password" placeholder="Password please"></yd-input>
          </yd-cell-item>
          
          <yd-cell-item>
              <yd-input slot="right" type="password" v-model="cpassword" placeholder="Confirm password please"></yd-input>
          </yd-cell-item>
    </yd-cell-group>
  
      <yd-button-group>
              <yd-button size="large" @click.native="signup">Sign up</yd-button>
      </yd-button-group>
  </div>
</template>

<script>
export default {
  data () {
    return {
      phone,
      password,
      cpassword,
    }
  },
  methods : {
    signup() {
      console.log('11')
    }
  }
}
</script>

<style lang="stylus" scoped>

</style>    




